            ta.type = 'text/javascript';
            ta.async = true;
            ta.src = document.location.protocol + '//' + 's3.pstatp.com/bytecom/resource/track_log/src/toutiao-track-log.js';
            ta.onerror = function () {
                var request = new XMLHttpRequest();
                var web_url = window.encodeURIComponent(window.location.href);
                var js_url = ta.src;
                var url = '//ad.toutiao.com/link_monitor/cdn_failed?web_url=' + web_url + '&js_url=' + js_url + '&convert_id=' + _toutiao;
                request.open('GET', url, true);
                request.send(null);
            }
            var s = document.getElementsByTagName('script')[0];
            s.parentNode.insertBefore(ta, s);
        })(window);  
    }
    function getKeyWord() {
        var str = document.referrer;
        if (str.indexOf('baidu.com') != -1) {
            var patt = /&wo?r?d=([\s\S]+?)&/i;
            var n = patt.exec(str);
            if (n == null) {
                return false;
            } else {
                return n[1];
            }
        } else if (str.indexOf('sogou.com') != -1) {
            if (str.indexOf('&query=') != -1) {
                var patt = /&query=([\s\S]+?)&/i;
                var n = patt.exec(str);
                if (n == null) {
                    var patt = /&query=([\s\S]+)/i;
                    var n = patt.exec(str);
                    console.log(n);
                    return n[1];
                } else {
                    console.log(n);
                    return n[1];
                }
            } else if (str.indexOf('&keyword=') != -1) {
                var patt = /&keyword=([\s\S]+?)&/i;
                var n = patt.exec(str);
                console.log(n);
                if (n == null) {
                    return false;
                } else {
                    return n[1];
                }
            }
        } else {
            return '';
        }
    }
